import {Component, OnInit} from '@angular/core';
import {MatCard} from "@angular/material/card";
import {MathJaxParagraphComponent} from "../math-jax-paragraph/math-jax-paragraph.component";
import {FormBuilder, FormControl, ReactiveFormsModule, Validators} from "@angular/forms";
import {MatButton} from "@angular/material/button";
import {MatFormField, MatInput, MatLabel} from "@angular/material/input";
import {MasodfokuService} from "../services/masodfoku.service";
import {NgIf} from "@angular/common";
import {HaromszogService} from "../services/haromszog.service";
import {Router, RouterLink} from '@angular/router';

@Component({
  selector: 'app-test-card',
  standalone: true,
  imports: [
    MatCard,
    MathJaxParagraphComponent,
    ReactiveFormsModule,
    MatButton,
    MatInput,
    MatFormField,
    MatLabel,
    NgIf,
    RouterLink
  ],
  templateUrl: './test-card.component.html',
  styleUrl: './test-card.component.css'
})


export class TestCardComponent implements OnInit{
  feladat:string = "";
  megoldas: number[] = [];
  menet:string = "";
  megoldott: boolean|undefined;
  private service: any;
  constructor(
    private router: Router,
    private formBuilder: FormBuilder,
    private masodfokuService: MasodfokuService,
    private haromszogService: HaromszogService,
  ) {
    this.setServiceBasedOnRoute();
  }

  private setServiceBasedOnRoute(): void {
    const currentRoute = this.router.url;
    if (currentRoute === '/masodfoku') {
      this.service = this.masodfokuService;
    } else if (currentRoute === '/haromszog') {
      this.service = this.haromszogService;
    } else {
      this.service = this.masodfokuService;
    }
  }


  solutionForm = this.formBuilder.group({
    solution: new FormControl(null, [Validators.required])
  })

  private arrayCompare(a:number[], b:number[]):boolean{
    if (a.length !== b.length) {
      return false;
    }
    return a.every((value, index) => value === b[index]);
  }

  onSubmit(): void {
    // Process checkout data here
    const input = this.solutionForm.value.solution ?? ""
    if (this.solutionForm.value.solution === null){
      return;
    }
    let result = input.split(",");
    const intArray: number[] = result.map(str => parseInt(str, 10));
    intArray.sort((a, b) => a - b);
    this.megoldas.sort((a,b)=> a - b);
    if(this.arrayCompare(intArray,this.megoldas)) {
      this.megoldott = true;
      return;
    }
    const floatArray: number[] = result.map(str => parseFloat(str)).map(num => parseFloat(num.toFixed(2)));
    floatArray.sort((a, b) => a - b);
    this.megoldas.sort((a,b)=> a - b);
    if(this.arrayCompare(floatArray,this.megoldas)) {
      this.megoldott = true;
      return;
    }
    this.megoldott=false;
  }

  ngOnInit(): void {
    const generaltFeladat = this.service.generateFeladat();
    this.feladat = decodeURI(generaltFeladat.Feladat)
    this.megoldas = generaltFeladat.Megoldas;
    this.menet = generaltFeladat.Menet
  }


  protected readonly window = window;
}
