import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class HaromszogService {

  constructor() { }

  private getRandomInt(min:number, max:number) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  generateFeladat(){
    let befogo;
    let atfogo1: number;
    let atfogo2: number;
    do {
      atfogo1 = this.getRandomInt(1, 250);
      atfogo2 = this.getRandomInt(1, 250);
      befogo = Math.sqrt(atfogo2 * atfogo2 + atfogo1 * atfogo1);
    }while(!Number.isInteger(befogo))

    return {"Feladat": `Van egy derékszögű háromszögünk, tudjuk a két átfogójának hosszát rendre ${atfogo1} és ${atfogo2}. Milyen hosszú a befogó?`,
      "Megoldas":[befogo],
      "Menet": `Mivel tudjuk a két befogót így a következő módon ki tudjuk számolni: \\( \\sqrt{${atfogo1}^2 + ${atfogo2}^2}  \\)`
    };
  }
}
