import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class MasodfokuService {
  constructor() {
  }

  private getRandomInt(min:number, max:number) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  generateFeladat(){
    let a,b,c:number;
    let root1, root2:number;
    root1 = this.getRandomInt(-1000,1000);
    root2 = this.getRandomInt(-1000,1000);
    a = 1;
    b = -1 * (root1 + root2);
    c = root1 * root2;

  return {"Feladat": `Oldjuk meg a következő másodfokú egyenletet:  \\(x^2 + ${b}x + ${c} = 0\\)`,
          "Megoldas":[root1,root2],
          "Menet": `Helyetesítsünk be a megoldóképletbe \\(x = {-b \\pm \\sqrt{b^2-4ac} \\over 2a}.\\) Ekkor a következő megoldásokat kapjuk:\\(Gyök\_1 = {${-b} - \\sqrt{(${b})^2-${4*a*c}} \\over 2}.\\) és \\(Gyök\_2 = {${-b} + \\sqrt{(${b})^2-${4*a*c}} \\over 2}.\\)`
    };
  }

}
